var _this,countDown;

export default {
	data() {
		return {
			phoneData:'', // 用户/电话
			passData:'', //密码
			showPassword:false, //密码是否显示
			second: 0, //倒计时
			verCode:"", //验证码
			showAgree:true, //协议是否选择
		}
	},
	mounted() {
		_this= this;
		clearInterval(countDown);//先清理一次循环，避免缓存
	},
	methods: {
		isShowPass() {
			//是否显示密码
		    _this.showPassword = !_this.showPassword
		},
		isShowAgree(){
			//是否选择协议
			_this.showAgree = !_this.showAgree;
		},
		getVerCode(){
			//获取验证码
			if (_this.phoneData.length != 11) {
			     uni.showToast({
			        icon: 'none',
					position: 'bottom',
			        title: '手机号不正确'
			    });
			    return false;
			}
			if(_this.second>0){
				return false;
			}
			_this.second = 60;
			countDown = setInterval(function(){
				_this.second--;
				if(_this.second==0){
					clearInterval(countDown)
				}
			},1000)
			console.log("获取验证码")
		},
	    startReg() {
			//注册
			if (this.showAgree == false) {
			    uni.showToast({
			        icon: 'none',
					position: 'bottom',
			        title: '请先同意《软件用户协议》'
			    });
			    return false;
			}
			if (this.phoneData.length !=11) {
			    uni.showToast({
			        icon: 'none',
					position: 'bottom',
			        title: '手机号不正确'
			    });
			    return false;
			}
	        if (this.passData.length < 6) {
	            uni.showToast({
	                icon: 'none',
					position: 'bottom',
	                title: '密码不正确'
	            });
	            return false;
	        }
			if (this.verCode.length != 4) {
			    uni.showToast({
			        icon: 'none',
					position: 'bottom',
			        title: '验证码不正确'
			    });
			    return false;
			}
			console.log("注册成功")
	    }
	},
	computed:{
		getVerCodeSecond(){
			//验证码倒计时计算
			if(this.second<=0){
				return '获取验证码';
			}else{
				if(this.second<10){
					return '0'+this.second;
				}else{
					return this.second;
				}
			}

		}
	},
}
